import React, { useState, useEffect } from 'react';
import Header from '../header';
import './skeleton.scss';

function Skeleton(props) {
    let initial = {'card-img':'','card-header':'','card-description':'','card-username':'','card-date':'','card-user-img':''};
    const [loading, setLoading] = useState(true);
    const [data, setData] = useState(initial);
    const [classload, setClassload] = useState('animated-bg');
    useEffect(()=>{
        setTimeout(function(){
            initial = {'card-img':'https://images.unsplash.com/photo-1621623207295-ee549608175e?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80','card-header':'Lorem Ipsum Header','card-description':'Lorem ipsum dolor sit amet consectetur adipisicing elit. Sed, sint.','card-username':'Mackral Gonsalves','card-date':'Oct 7, 2021','card-user-img':'https://pbs.twimg.com/profile_images/718655882372845569/_020fkxr.jpg'};
            setData(initial);
            setLoading(false);
            setClassload('');
        },5000);     
    },[]);  
    return (
        <>
            <Header number={props.number} />
            <div className="App-container" style={{'backgroundColor':'#ecf0f1', 'minHeight':'calc(100vh - 30px)', 'display':'flex', 'alignItems': 'center', 'justifyContent':'center'}}>
                <div className="card-container">
                    <div className={`card-img ${classload}`} style={{backgroundImage: `url(${data['card-img']})`}}></div>
                    <div className="card-description">
                        <div className={`card-header ${classload}`}>{data['card-header']}</div>
                        <div className={`card-description-text ${classload}`}>{data['card-description']}</div>
                        <div className="card-author">
                            <div className={`card-user-img ${classload}`} style={{backgroundImage: `url(${data['card-user-img']})`}}></div>
                            <div className="card-user-details">
                                <div className={`card-username ${classload}`}>{data['card-username']}</div>
                                <div className={`card-date ${classload}`}>{data['card-date']}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Skeleton;
