import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import Header from '../header';
import './pokedex.scss';

function Pokemon(props) {
    const [pokemonData, setPokemonData] = useState('');
    const [error, setError] = useState(null);
    const colors = {
        fire: '#FDDFDF',
        grass: '#DEFDE0',
        electric: '#FCF7DE',
        water: '#DEF3FD',
        ground: '#f4e7da',
        rock: '#d5d5d4',
        fairy: '#fceaff',
        poison: '#98d7a5',
        bug: '#f8d5a3',
        dragon: '#97b3e6',
        psychic: '#eaeda1',
        flying: '#F5F5F5',
        fighting: '#E6E0D4',
        normal: '#F5F5F5'
    };
    let id = props.match.params.id;
    useEffect(()=>{
        window.scrollTo(0, 0);
        let url = `https://pokeapi.co/api/v2/pokemon/${id}`;
        fetch(url)
        .then(res => res.json())
        .then(
            (result) => {
                console.log(result);                
                setPokemonData(result);
            },
            (error) => {
                setError({error});
            }
        );
    },[]);
    console.log(pokemonData);
    return (
        <>
            <Header number='006' />
            <div className="App-container">
                <div className="pokemon-container">                    
                    {(error)?
                        <div>{error}</div>:
                        (pokemonData)?
                        <>
                            <div className="pokemon-detail-view">
                                <>
                                    {console.log(pokemonData['abilities'])}
                                    <div className="pokemon-item" style={{backgroundColor : colors[pokemonData['types'][0]['type']['name']]}}>
                                        <div className="pokemon-name">#{pokemonData['id']} {pokemonData['name']}</div>
                                        <img src={`https://pokeres.bastionbot.org/images/pokemon/${pokemonData['id']}.png`} alt="" className="pokemon-img" />
                                        <div className="pokemon-thumbs">
                                            <img src={pokemonData['sprites']['back_default']} alt="" className="pokemon-thumb-img" />
                                            <img src={pokemonData['sprites']['front_default']} alt="" className="pokemon-thumb-img" />
                                        </div>
                                        <div>
                                            <h4>Abilities</h4>
                                            <div className="abilities-container">
                                                {Object.keys(pokemonData['abilities']).map((item, i)=>(
                                                    <span key={i} className="abilities-text">{pokemonData['abilities'][item]['ability']['name']}</span>
                                                ))}
                                            </div>
                                        </div>
                                        <div>
                                            <h4>Moves</h4>
                                            <div className="abilities-container">
                                                {Object.keys(pokemonData['moves']).map((item, i)=>(
                                                    // <span>{item}</span>
                                                    <span key={i} className="abilities-text">{pokemonData['moves'][item]['move']['name']}</span>
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                </>
                            </div>
                            <div className="pagination">
                                <Link to="/day-006">Back to list</Link>
                            </div>
                        </>
                        :<div></div>
                    }
                </div>
            </div>
        </>
    );
}

export default Pokemon;
