import React from "react";
import {
  // BrowserRouter as Router,
  HashRouter,
  Switch,
  Route
} from "react-router-dom";
import './App.scss';
import MenuList from './Components/list-menu';
import Footer from './Components/footer';
import Home from './Components/home/Home';
import Insta from './Components/day-001/Insta';
import Count from './Components/day-002/Count';
import MyMeal from './Components/day-003/MyMeal';
import Clock from './Components/day-004/Clock';
import DigitalClock from './Components/day-005/DigitalClock';
import Pokedex from "./Components/day-006/Pokedex";
import Pokemon from "./Components/day-006/Pokemon";
import Skeleton from "./Components/day-007/Skeleton";

function App() {
  let data = [
     {
       "project_id": "001",
       "project_name": "Insta Feed",
       "project_component": Insta,
       "project_url": "day-001",
       "project_description": "Created a simple Insta style image feed to start the challenge."
      },
     {
       "project_id": "002",
       "project_name": "Visitor Count",
       "project_component": Count,
       "project_url": "day-002",
       "project_description": "Created a counter that keeps a track of visitor count on page. Added a menu for easier navigation of projects, a homepage for a better view, an URL for online view and changed the routes to support Github üòû"
      },
      {
        "project_id": "003",
        "project_name": "Random Meal",
        "project_component": MyMeal,
        "project_url": "day-003",
        "project_description": "Fetching data from a public API and display on click of button."
       },
      {
        "project_id": "004",
        "project_name": "Analog Clock",
        "project_component": Clock,
        "project_url": "day-004",
        "project_description": "Created an analog clock using CSS + Javascript."
       },
      {
        "project_id": "005",
        "project_name": "Digital Clock",
        "project_component": DigitalClock,
        "project_url": "day-005",
        "project_description": "Created a digital clock using CSS + Javascript. (Out of time today so opted for a fast coding option üòú)"
       },
      {
        "project_id": "006",
        "project_name": "Pokedex",
        "project_component": Pokedex,
        "project_url": "day-006",
        "project_description": "Created a list view of pokemons with custom pagination. Handling api data for custom pagination was a task."
       },
       {
         "project_id": "007",
         "project_name": "Loader",
         "project_component": Skeleton,
         "project_url": "day-007",
         "project_description": "Created this loading effect to avoid/solve Cumulative Layout Shift."
        },
  ];
  return (
    <div className="App">
      <HashRouter>
        <>
          <MenuList menu={data} />
          <Switch>  
            <Route path={`/day-006/:id`} component={Pokemon}></Route>          
            {
              data.map((value, key)=>(
                // console.log(value.project_component),
                <Route path={`/${value.project_url}`} key={key}>
                  {
                    (value.project_id==="000")?
                    React.createElement(value.project_component, {
                      to : value.url, number : value.project_id, project : JSON.stringify(data)
                    }):
                    React.createElement(value.project_component, {
                      to : value.url, number : value.project_id
                    })
                  }
                </Route>
              ))
            }
            <Route path={`/`}>
              <Home number="000" project={JSON.stringify(data)} />
            </Route>          
          </Switch>
          <Footer />
        </>
      </HashRouter>
    </div>
  );
}

export default App;
