import React, { useState, useEffect } from 'react';
import { Link } from "react-router-dom";
import Header from '../header';
import './pokedex.scss';

function Pokedex(props) {
    function getPokemonData(offset, limit){
        let url = `https://pokeapi.co/api/v2/pokemon/?limit=${limit}&offset=${offset}`;
        let arr = {};
        fetch(url)
        .then(res => res.json())
        .then(
            (result) => {
                console.log(result);
                let json = (result.results);
                let j = 0;
                let k = {};
                for(let i=offset; i<(offset+limit); i++){
                    arr[i+1] = json[j];
                    j++;
                    k = '';
                }
                setPokemonData(arr);
            },
            (error) => {
                setError({error});
            }
        );
    }
    const [pokemonData, setPokemonData] = useState('');
    const [pageNo, setPageNo] = useState(0);
    const [offset, setOffset] = useState(9);
    const [error, setError] = useState(null);
    useEffect(()=>{
        window.scrollTo(0, 0);
        getPokemonData(pageNo , offset);
    },[pageNo, offset]);
    function prevPage(){
        if(pageNo>1){
            setPageNo(pageNo-offset);
        }
    }
    function nextPage(){
        setPageNo(pageNo+offset);
    }
    return (
        <>
            <Header number={props.number} />
            <div className="App-container">
                <div className="pokemon-container">                    
                    {(error)?
                        <div>{error}</div>:
                        (pokemonData)?
                        <>
                            <div className="pokemon-list">
                                <>
                                    {Object.keys(pokemonData).map((item, i)=>(
                                        <Link to={`/day-006/${pokemonData[item]['name']}`} className="pokemon-item" key={item}>
                                            <div className="pokemon-id">#{item}</div>
                                            <div className="pokemon-name">{pokemonData[item]['name']}</div>
                                            <img src={`https://pokeres.bastionbot.org/images/pokemon/${item}.png`} alt="" className="pokemon-img" />
                                        </Link>
                                    ))}
                                </>
                            </div>
                            <div className="pagination">
                                <div className={`${(pageNo<offset)?'disabled':''}`} onClick={prevPage}>Prev</div>
                                <div className={`${(pageNo<1118)?'':'disabled'}`} onClick={nextPage}>Next</div>
                            </div>
                        </>
                        :<div>{error}</div>
                    }
                </div>
            </div>
        </>
    );
}

export default Pokedex;
